import { useState, useEffect, useRef } from 'react';
import SideBar from '../components/Admin/Sidebar';
import { Toast } from 'primereact/toast';

import { Splitter, SplitterPanel } from 'primereact/splitter';
import { Card } from 'primereact/card';
import { InputText } from 'primereact/inputtext';
import { InputNumber } from 'primereact/inputnumber';
import { Button } from 'primereact/button';
import { DataTable } from 'primereact/datatable';
import { Column } from 'primereact/column';

import axios from 'axios';

const Settings = () => {

    const toast = useRef(null);

    const [password, $password] = useState('...');

    const [minSumBet, setMinSumBet] = useState();
    const [minKefBet, setMinKefBet] = useState();

    const [numberBot, setNumberBot] = useState();
    const [numberUser, setNumberUser] = useState();
    const [numbersForUsers, setNumbersForUsers] = useState([]);
    const [numbersForBots, setNumbersForBots] = useState([]);

    // Get universal password
    useEffect(() => {
        async function getData() {

            const req = await axios.post('/api/find/settings');

            if (req.data.ok) {

                $password(req.data.password);
                setNumbersForUsers(req.data.arrNumsUsers);
                setNumbersForBots(req.data.arrNumsBots);

                setMinKefBet(req.data.minKefOdds);
                setMinSumBet(req.data.minAmountOdds);

            } else {
                toast.current.show({ severity: 'error', summary: 'Error Message', detail: req.data.text, life: 5000});
            }
        }
        getData();
        
    }, [])

    async function setNewPassword() {

        const params = {
            name: 'universalPassword',
            data: password
        }
        
        const request = await fetch('/api/setting/password', {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json'
            },
            body: JSON.stringify(params)
        });

        const response = await request.json();
        if (!response.ok) {
            toast.current.show({ severity: 'error', summary: 'Warning Message', detail: response.message, life: 5000});
        } else {
            toast.current.show({ severity: 'success', summary: 'Info Message', detail: 'Password change succefull', life: 5000});
        }
    }

    async function addNumberToList(e) {

        async function saveNumberToDB(value, type) {
            let req = await axios.post('/api/setting/numbers', {
                type, value
            });

            if (req.data.ok) {
                toast.current.show({ severity: 'success', summary: 'Info Message', detail: 'Number saved', life: 5000});
                return true;
            } else {
                toast.current.show({ severity: 'error', summary: 'Error Message', detail: req.data.text, life: 5000});
                return false;
            }
        }

        let id = e.target.id;

        if (id === 'addNumberBot') {
            let val = numberBot;
            if (val < 1) {
                toast.current.show({ severity: 'warn', summary: 'Warning Message', detail: 'The number must not be less than 1', life: 5000});
                return;
            }

            const status = await saveNumberToDB(val, 'Bots');
            if (status) setNumbersForBots([...numbersForBots, { 'number': val }]);
        } else if (id === 'addNumberUser') {
            let val = numberUser;
            if (val < 1) {
                toast.current.show({ severity: 'warn', summary: 'Warning Message', detail: 'The number must not be less than 1', life: 5000});
                return;
            }

            const status = await saveNumberToDB(val, 'Users');
            if (status) setNumbersForUsers([...numbersForUsers, { 'number': val }]);
        }

        setNumberBot(0);
        setNumberUser(0);
    }

    async function changeMinAmountBet(e) {
        let req = await axios.post('/api/setting/min_amount_odds', {
            val: minSumBet
        });

        if (req.data.ok) {
            toast.current.show({ severity: 'success', summary: 'Info Message', detail: 'The minimum bet amount is set', life: 5000});
        } else {
            toast.current.show({ severity: 'error', summary: 'Error Message', detail: req.data.text, life: 5000});
        }
    }

    async function changeMinKefOdds(e) {
        let req = await axios.post('/api/setting/min_kef_odds', {
            val: minKefBet
        });

        if (req.data.ok) {
            toast.current.show({ severity: 'success', summary: 'Info Message', detail: 'Minimum bet odds set', life: 5000});
        } else {
            toast.current.show({ severity: 'error', summary: 'Error Message', detail: req.data.text, life: 5000});
        }
    }

    function templateIconAction(rowData) {
        async function handlerRemoveNumber(e) {
            const row = e.target.closest('tr');
            let index = row.rowIndex;

            let tableBots = e.target.closest('#Numbers-Bots') !== null;
            let nameSettings = 'Numbers-Users';
            if (tableBots) nameSettings = 'Numbers-Bots';

            const req = await axios.post('/api/setting/remove-numbers', {
                name: nameSettings,
                index: index
            });

            if (req.data.ok) {
                toast.current.show({ severity: 'success', summary: 'Info Message', detail: 'Number removed', life: 5000});
                row.remove();
            } else {
                toast.current.show({ severity: 'error', summary: 'Error Message', detail: req.data.text, life: 5000});
            }

        }
        return <Button onClick={handlerRemoveNumber} className='p-button p-component p-button-rounded p-button-danger p-button-text p-button-icon-only' icon="pi pi-times" />;
    }

    return (
        <div className='block_admin'>
            <SideBar />
            <div className="admin_content">
                <div className="admin_content_block admin_content_block--max">
                    <Splitter style={{height: '100%'}}>
                        <SplitterPanel className='p-3'>
                            <Splitter style={{ height: '100%' }} layout='vertical'>
                                <SplitterPanel minSize={33} className='p-3'>
                                    <Card title="Universal password for Bots">
                                        <div className="field">
                                            <p className='mb-3'>Current Password: <span>{ password }</span></p>
                                        </div>
                                        <div className="formgrid grid">
                                            <div className="field col">
                                                <InputText className="p-inputtext-sm mb-2 w-12" value={password} onChange={(e) => $password(e.target.value)} placeholder='type ...' />
                                            </div>
                                            <div className="field col">
                                                <Button className='p-button-success p-button' onClick={ setNewPassword }>Change</Button>
                                            </div>
                                        </div>
                                    </Card>
                                </SplitterPanel>
                                <SplitterPanel minSize={33} className='p-3'>
                                    <Card title="Minimum bet amount">
                                        <div className="field">
                                            <p className='mb-3'>Minimum bet amount: <span>{ minSumBet }</span></p>
                                        </div>
                                        <div className="formgrid grid">
                                            <div className="field col">
                                                <InputNumber mode="decimal" minFractionDigits={2} className="p-inputtext-sm mb-2 w-12" value={minSumBet} onChange={(e) => setMinSumBet(e.value)} placeholder='type ...' />
                                            </div>
                                            <div className="field col">
                                                <Button onClick={changeMinAmountBet} className='p-button-success p-button'>Change</Button>
                                            </div>
                                        </div>
                                    </Card>
                                </SplitterPanel>
                                <SplitterPanel minSize={33} className='p-3'>
                                    <Card title="Minimum bet odds">
                                        <div className="field">
                                            <p className='mb-3'>Minimum bet odds: <span>{ minKefBet }</span></p>
                                        </div>
                                        <div className="formgrid grid">
                                            <div className="field col">
                                                <InputNumber mode="decimal" minFractionDigits={2} className="p-inputtext-sm mb-2 w-12" value={minKefBet} onChange={(e) => setMinKefBet(e.value)} placeholder='type ...' />
                                            </div>
                                            <div className="field col">
                                                <Button onClick={changeMinKefOdds} className='p-button-success p-button'>Change</Button>
                                            </div>
                                        </div>
                                    </Card>
                                </SplitterPanel>
                            </Splitter>
                        </SplitterPanel>
                        <SplitterPanel className='p-3'>
                            <Card title="List Numbers for Bots">
                                <div className="formgrid grid">
                                    <div className="field col">
                                        <InputNumber mode="decimal" minFractionDigits={2} value={numberBot} onValueChange={(e) => setNumberBot(e.value)} className='p-inputtext-sm w-12' placeholder='type number' />
                                    </div>
                                    <div className="field col">
                                        <Button onClick={ addNumberToList } className='p-button-success' id='addNumberBot'>Add Number</Button>
                                    </div>
                                </div>
                                <DataTable id='Numbers-Bots' value={numbersForBots} size="small">
                                    <Column field="number" header="Number"></Column>
                                    <Column field="icon" header="Action" align="center" body={ templateIconAction }></Column>
                                </DataTable>
                            </Card>
                        </SplitterPanel>
                        <SplitterPanel className='p-3'>
                            <Card title="List Numbers for Users">
                                <div className="formgrid grid">
                                    <div className="field col">
                                        <InputNumber mode="decimal" minFractionDigits={2} value={numberUser} onValueChange={(e) => setNumberUser(e.value)} className='p-inputtext-sm w-12' placeholder='type number' />
                                    </div>
                                    <div className="field col">
                                        <Button onClick={ addNumberToList } className='p-button-success' id='addNumberUser'>Add Number</Button>
                                    </div>
                                </div>
                                <DataTable id='Numbers-Users' value={numbersForUsers} size="small">
                                    <Column field="number" header="Number"></Column>
                                    <Column field="icon" header="Action" align="center" body={ templateIconAction }></Column>
                                </DataTable>
                            </Card>
                        </SplitterPanel>
                    </Splitter>
                </div>
            </div>
            <Toast ref={ toast } />
        </div>
    )
}

export default Settings