import { Card } from 'primereact/card';
import { InputText } from 'primereact/inputtext';
import { Button } from 'primereact/button';

import { Toast } from 'primereact/toast';

import { useState, useEffect, useRef } from 'react';
import axios from 'axios';

const Settings = () => {
    const [email, setEmail] = useState('');
    const [passwordEmail, setPasswordEmail] = useState('');

    const [oldPassword, setOldPassword] = useState('');
    const [newPassword, setNewPassword] = useState('');
    const [confirmPassword, setConfirmPassword] = useState('');

    const toast = useRef(null);

    useEffect(() => {
        (async function getData() {
            const req = await axios.post('/api/find/email');
            if (req.data.ok) {
                setEmail(req.data.email);
            }
        })();

    }, []);

    async function changeEmail() {
        let req = await axios.post('/api/auth/change-email', {
            email: email,
            password: passwordEmail
        });

        if (req.data.ok) {
            toast.current.show({ severity:'success', summary: 'Success', detail: 'Email changed successfully', life: 5000 });
            return;
        } else {
            toast.current.show({severity:'error', summary: 'Error Message', detail: req.data.text, life: 5000});
            return;
        }
    }
    async function changePassword() {

        if (!oldPassword || !newPassword || !confirmPassword) {
            toast.current.show({severity:'warn', summary: 'Warning Message', detail: 'Fill in all fields related to password change', life: 5000});
            return;
        }

        let req = await axios.post('/api/auth/change-password', {
            oldPassword: oldPassword, 
            newPassword: newPassword, 
            confirmPassword: confirmPassword
        });

        if (req.data.ok) {
            toast.current.show({ severity:'success', summary: 'Success', detail: 'Password changed successfully', life: 5000 });
        } else {
            toast.current.show({severity:'error', summary: 'Error Message', detail: req.data.text, life: 5000});
        }
    }

    return (
        <div>
            <Card title="Change Password" style={{ width: '100%', marginBottom: '20px', padding: '10px 10px 0px 10px' }}>
                <div className="grid p-fluid">
                    <div className="col-12">
                        <div className="field mb-0">
                            <label htmlFor="oldPassword" className="block mb-2">Old Password</label>
                            <InputText value={oldPassword} onChange={ (e) => setOldPassword(e.target.value) } onInput id='oldPassword' placeholder='My answer' className="block" />
                        </div>
                    </div>
                    <div className="col-12">
                        <div className="field mb-0">
                            <label htmlFor="newPassword" className="block mb-2">New Password</label>
                            <InputText value={newPassword} onChange={ (e) => setNewPassword(e.target.value) } id='newPassword' placeholder='My answer' className="block" />
                        </div>
                    </div>
                    <div className="col-12">
                        <div className="field mb-0">
                            <label htmlFor="confirmPassword" className="block mb-2">Confirm Password</label>
                            <InputText value={confirmPassword} onChange={ (e) => setConfirmPassword(e.target.value) } id='confirmPassword' placeholder='My answer' className="block" />
                        </div>
                    </div>
                    <div className="col-12">
                        <div className="field mb-0 flex justify-content-end">
                            <Button onClick={changePassword} className="p-button-success justify-content-center w-3">Change</Button>
                        </div>
                    </div>
                </div>
            </Card>
            <Card title="Change Email" style={{ width: '100%', marginBottom: '20px', padding: '10px 10px 0px 10px' }}>
                <div className="grid p-fluid">
                    <div className="col-12">
                        <div className="field mb-0">
                            <label htmlFor="newEmail" className="block mb-2">New Email</label>
                            <InputText id='newEmail' value={ email } onInput={(e) => setEmail(e.target.value)} placeholder='My answer' className="block" />
                        </div>
                    </div>
                    <div className="col-12">
                        <div className="field mb-0">
                            <label htmlFor="confirmEmailPassword" className="block mb-2">Confirm Password</label>
                            <InputText id='confirmEmailPassword' value={passwordEmail} onInput={(e) => setPasswordEmail(e.target.value)} placeholder='My answer' className="block" />
                        </div>
                    </div>
                    <div className="col-12">
                        <div className="field mb-0 flex justify-content-end">
                            <Button onClick={ changeEmail } className="p-button-success justify-content-center w-3">Change</Button>
                        </div>
                    </div>
                </div>
            </Card>
            <Toast ref={toast} />
        </div>
    )
}

export default Settings