import React, { useState, useEffect } from "react";
import { LineChart, Line, ResponsiveContainer, XAxis, YAxis } from 'recharts';

const yVal = 2.45; // Переменная контролирующая Y ось значений
const xVal = 9;

const dataDefault = [
  { x: 1, y: 1, val: 1 },
  { x: xVal, y: yVal }
];

const Game = (payload) => {

  const grafdata = payload.data;

  const [value, $value] = useState(1);
  const [data, $data] = useState(dataDefault);
  const [values, $values] = useState([]);
  let [count, $count] = useState(0);

  const [clasGame, setClasGame] = useState('');

  useEffect(() => {

    let status = grafdata.gameStatus;
    let nullValues = [];

    if (status === 'Game') {
      setClasGame('status--game');
    }
    if (status === 'Results') {
      setClasGame('status--game-crash');
    }
    if (status === 'Pause') {
      setClasGame('status--game-pause');
    }

    if (status === 'Game') {
      if (count * 100 / 1000 >= xVal) {
        if (value > yVal) {
          nullValues = [ { x: count * 100 / 1000, y: value } ];
        } else {
          nullValues = [ { x: count * 100 / 1000, y: yVal } ];
        }
      } else {
        if (value >= yVal) {
          nullValues = [ { x: xVal, y: value } ];
        } else {
          nullValues = [ { x: xVal, y: yVal } ];
        }
      }

      $values([...values, ...[ { val: value, y: value, x: count * 100 / 1000 } ]]);
      $data([...values, ...nullValues]);

    } else if (status === 'Results') {
      $values([]);
      $data(dataDefault);
    }

  }, [count]);

  useEffect(() => {

    if (grafdata.recovery) {
      let status = grafdata.gameStatus;

      if (status === 'Game') {

        let _value = grafdata.value;
        let _count = grafdata.count;

        let nullValues = [];
        if (_count * 100 / 1000 >= xVal) {
          if (_value > yVal) {
            nullValues = [ { x: _count * 100 / 1000, y: _value } ];
          } else {
            nullValues = [ { x: _count * 100 / 1000, y: yVal } ];
          }
        } else {
          if (_value >= yVal) {
            nullValues = [ { x: xVal, y: _value } ];
          } else {
            nullValues = [ { x: xVal, y: yVal } ];
          }
        }

        let _values = [];

        let val = 100;
        let step = 1;

        for (let i = 1; i < _count; i++) {

          val = val + step;
          if (Math.trunc(val) % 10 == 0) {
            step += 0.07;
          }
          _values.push({ val: Number(val / 100).toFixed(2), y: Number(val / 100).toFixed(2), x: i * 100 / 1000 });

        }

        $values(_values);
        $data([..._values, ...nullValues]);

      }

    }

    $value(grafdata.value);
    $count(grafdata.count);

  }, [ payload.data ])

  return (
    <div className="graph_body">
      <div className="graph_main">
        <ResponsiveContainer width="100%" height="100%">
          <LineChart width="100%" height="100%" data={data} margin={{ top: 5, right: 30, left: -25, bottom: -10 }}>
            <YAxis type="Number" dataKey="y" unit='x' tickCount={4} />
            <XAxis type="Number" dataKey="x" tickCount={8} />
            <Line type="basis" dataKey="val" stroke="#bebebe" strokeWidth={3} dot={false} />
          </LineChart>
        </ResponsiveContainer>
      </div>
      <div className="game__graph__value"><p className={ clasGame }>{ payload.value }</p></div>
    </div>
  )
}

export default Game;