import React from "react"

const Table = data => {
  const dataTable = data.data

  let status = false;
  if (data.clasColor) status = true;

  return (
    <table>
      <thead>
        <tr>
          {
            dataTable.head.map((el, index) => (
              <th key={'thead' + index} scope="col">{el}</th>
            ))
          }
        </tr>
      </thead>
      <tbody>
        {
          dataTable.body.map((element, index) => (

            <tr className={

              (() => {
                try {

                  let val = element[element.length - 1];
                  if (!val) return '';

                  if (status) {
                    if (val.indexOf('...') !== -1) {
                      return '';
                    } else {
                      if (val.indexOf('-') === -1) return 'color-green';
                      return 'color-red';
                    }
                  } else {
                    return '';
                  }
                } catch(e) {
                  return '';
                }
              })()

            } key={'tbody' + index}>
              {
                element.map((el, ind) => (
                  <td key={ind+'_'+index}>{el}</td>
                ))
              }
            </tr>
          ))
        }

      </tbody>
    </table>
  )
}



export default Table