import { Dialog } from 'primereact/dialog';

import { InputNumber } from 'primereact/inputnumber';
import { Card } from 'primereact/card';
import { Button } from 'primereact/button';
import { useEffect, useState, useRef } from 'react';

import { Toast } from 'primereact/toast';

import axios from 'axios';

const ModalWindow = (data) => {

    const [btc, setBtc] = useState(null);
    const [eth, setEth] = useState(null);

    const toast = useRef(null);

    async function saveValuesCurrency() {
        const req = await axios.post('/api/setting/set-currency-value', {
            btc: btc,
            eth: eth,
            login: data.userName
        });

        if (req.data.ok) {
            toast.current.show({ severity:'success', summary: 'Success', detail: 'Balance topped up', life: 5000 });
        } else {
            toast.current.show({severity:'error', summary: 'Error Message', detail: req.data.text, life: 5000});
        }
    }

    useEffect(() => {
        async function getCurrencyValue() {
            if (!data.userName) return;
            const req = await axios.post('/api/find/balance', { login: data.userName });
            if (req.data.ok) {
                setBtc(req.data.balance.btc);
                setEth(req.data.balance.eth);
            } else {
                toast.current.show({severity:'error', summary: 'Error Message', detail: req.data.text, life: 5000});
            }
        }
        getCurrencyValue();
    }, [data]);

    return (
        <Dialog maximizable={true} header={ data.header + ' ' + data.userName } dismissableMask={ data.dismissableMask } position={ data.position } style={ data.style } visible={ data.visible } resizable={ data.resizable } onHide={ data.onHide }>
            
            <Card title="Change Password" style={{ width: '100%', padding: '10px 10px 0px 10px' }}>
            <div className="grid p-fluid">
                <div className="col-12">
                    <div className="field mb-0">
                        <label htmlFor="oldPassword" className="block mb-2">BTC value</label>
                        <InputNumber value={btc} onChange={(e) => setBtc(e.value)} className="w-12" mode="decimal" minFractionDigits={5} maxFractionDigits={5} />
                    </div>
                </div>
                <div className="col-12">
                    <div className="field mb-0">
                        <label htmlFor="newPassword" className="block mb-2">ETH value</label>
                        <InputNumber value={eth} onChange={(e) => setEth(e.value)} className="w-12" mode="decimal" minFractionDigits={5} maxFractionDigits={5} />
                    </div>
                </div>
                <div className="col-12">
                    <div className="field mb-0 flex justify-content-end">
                        <Button onClick={saveValuesCurrency} className="p-button-success justify-content-center w-3">Save</Button>
                    </div>
                </div>
            </div>
            </Card>

            <Toast ref={toast} />
        </Dialog>
    )
}

export default ModalWindow