import { useRef, useEffect, useState, Fragment } from "react";
import { Toast } from 'primereact/toast';

import { InputNumber } from 'primereact/inputnumber';
import { Button } from "primereact/button";

const BlockBet = (data) => {

    const [sumBet, setSumBet] = useState(null);
    const [kefBet, setKefBet] = useState(null);

    const [minAmount, setMinAmount] = useState(0);
    const [minKef, setMinKef] = useState(0);

    const [isLoginUser, setIsLoginUser] = useState(false);

    const [buttonBet, setButtonBet] = useState('');

    const socket = data.socket;

    let toast = useRef(null);

    function changeStateCurrency(e) {
        document.querySelector('.active').classList.remove('active');
        e.target.classList.add('active');
    }

    function setBet(e) {

        let token = localStorage.getItem('Token');
        if (token) token = JSON.parse(token);

        let valSumBet = document.querySelector('#sum-bet input').value;
        let valKefBet = document.querySelector('#kef-bet input').value;
        let valBetCurrency = document.querySelector('.game_bet_block_row__currency.active').textContent;

        if (!valKefBet || !valSumBet) {
            toast.current.show({severity:'warn', summary: 'Warning Message', detail: 'Bet amount and odds are not filled', life: 5000});
            return;
        } else if (valSumBet <= 0) {
            toast.current.show({severity:'warn', summary: 'Warning Message', detail: 'Bet amount must be > 0', life: 5000});
            return;
        } else if (valKefBet <= 0) {
            toast.current.show({severity:'warn', summary: 'Warning Message', detail: 'Auto cash out must be > 0', life: 5000});
            return;
        }

        valSumBet = Number(valSumBet.replace(',', '.'));
        valKefBet = Number(valKefBet.replace(',', '.'));

        let payload = { token, sumBet: valSumBet, kefBet: valKefBet, currencyBet: valBetCurrency }

        socket.emit('setBet', payload);

        setButtonBet(<Fragment><button className="set_bet" disabled={ true } onClick={ setBet } >BET</button></Fragment>);

    }

    function abortGame(e) {
        let token = localStorage.getItem('Token');
        if (token) token = JSON.parse(token);

        let payload = { token };

        socket.emit('abortBet', payload);

        setButtonBet(<Fragment><button className="set_bet" disabled={true}>STOP</button></Fragment>);

    }

    function minAmountAction(e) {
        let id = e.target.id;
        if (!id) id = e.target.parentElement.id;

        let list = {
            'third': (val) => { return val / 3},
            'half': (val) => { return val / 2},
            'max': (val) => { return val},
        }

        let currency = document.querySelector('.game_bet_block_row__currency.active').id;

        let val = 0;

        let balance = JSON.parse(localStorage.getItem('Balance'));
        if (balance) {
            currency = String(currency).toLowerCase();

            if (currency in balance) {
                val = balance[currency];
            }
        }

        val = list[id](val);

        setSumBet(val);

    }

    useEffect(() => {

        let balance = JSON.parse(localStorage.getItem('Balance'));

        if (!balance) balance = { btc: 0, eth: 0 };

        setButtonBet(<Fragment><button className="set_bet" disabled={ balance.btc === 0 && balance.eth === 0 } onClick={ setBet } >BET</button></Fragment>);

        setIsLoginUser(localStorage.getItem('Token'));

        socket.emit('getBalance');

        socket.on('setBet:result', data => {
            if (data.ok) {
                toast.current.show({ severity:'success', summary: 'Success', detail: 'Bid accepted. Good luck!', life: 5000 });
            } else {
                if (data.access === false) {
                    
                    localStorage.removeItem('Token');
                    window.location.reload();
    
                }
    
                toast.current.show({ severity:'error', summary: 'Error Message', detail: data.text, life: 5000 });
            }
        });

        socket.on('getBalance', data => {

            balance = data.balance;
            let { minAmount, minKef } = data;

            localStorage.setItem('Balance', JSON.stringify(balance));

            setMinAmount(minAmount);
            setMinKef(minKef);

            setButtonBet(<Fragment><button className="set_bet" disabled={ balance.btc === 0 && balance.eth === 0 } onClick={ setBet } >BET</button></Fragment>);
        })

    }, []);

    useEffect(() => {

        if (data.isUserPlays) {
            if (data.gameStatus === 'Game') {
                setButtonBet(<Fragment><button className="set_bet abortGame" onClick={ abortGame }>STOP</button></Fragment>);
            } else {
                setButtonBet(<Fragment><button className="set_bet" disabled={ true } onClick={ setBet } >BET</button></Fragment>);
            }
        } else {
            let balance = JSON.parse(localStorage.getItem('Balance'));
            if (balance) {
                setButtonBet(<Fragment><button className="set_bet" disabled={ balance.btc === 0 && balance.eth === 0 } onClick={ setBet } >BET</button></Fragment>);
            }
        }

    }, [data.gameStatus, data.isUserPlays])

    if (isLoginUser != null && isLoginUser != false) {

        //console.log(`${new Date().toLocaleTimeString('ru-RU')}`);
        //console.log(data);

        

        const windowInnerWidth = window.innerWidth;

        if (windowInnerWidth < 700) {
            return(
                <div className="game_bet_block">
                    <div className="game_bet_block_row">
                        <p>Bet</p>
                        <InputNumber value={sumBet} onChange={(e) => setSumBet(e.value)} id="sum-bet" className="w-12" mode="decimal" minFractionDigits={0} maxFractionDigits={5} min={minAmount} useGrouping={false} placeholder={`Mindestwert ${minAmount}`} />
                        <div className="game_bet_block_row__currency active" id="BTC" onClick={ changeStateCurrency }>BTC</div>
                        <div className="game_bet_block_row__currency" id="ETH" onClick={ changeStateCurrency }>ETH</div>
                    </div>
                    <div className="game_bet_block_row">
                        <p>Auto cash out</p>
                        <InputNumber value={kefBet} onChange={(e) => setKefBet(e.value)} id="kef-bet" className="w-12" mode="decimal" minFractionDigits={2} maxFractionDigits={2} step={0.01} showButtons={true} min={minKef} useGrouping={false} placeholder={`Mindestwert ${minKef}`} />
                    </div>
                    <div className="game_bet_block_row">
                        {buttonBet}
                    </div>
                    <Toast ref={ toast } />
                </div>
            );
        }

        return(
            <div className="game_bet_block">
                <div className="game_bet_block_row">
                    <div className="game_bet_block_row__currency active" id="BTC" onClick={ changeStateCurrency }>BTC</div>
                    <div className="game_bet_block_row__currency" id="ETH" onClick={ changeStateCurrency }>ETH</div>
                </div>
                <div className="game_bet_block_row flex flex-column w-12">
                    <div className="flex w-12 align-items-center">
                        <p>Bet</p>
                        <InputNumber value={sumBet} onChange={(e) => setSumBet(e.value)} id="sum-bet" className="w-12" mode="decimal" minFractionDigits={0} maxFractionDigits={5}  min={minAmount} useGrouping={false} placeholder={`Mindestwert ${minAmount}`} />  
                    </div>
                    <div className="flex mt-1 w-12 justify-content-between">
                        <Button onClick={minAmountAction} id="third" label="1/3" className="my-btn p-button p-button-success w-4 h-1rem" />
                        <Button onClick={minAmountAction} id="half" label="1/2" className="my-btn p-button p-button-success w-4 h-1rem mx-2" />
                        <Button onClick={minAmountAction} id="max" label="max" className="my-btn p-button p-button-success w-4 h-1rem" />
                    </div>
                </div>
                <div className="game_bet_block_row">
                    <p>Auto cash out</p>
                    <InputNumber value={kefBet} onChange={(e) => setKefBet(e.value)} id="kef-bet" className="w-12" mode="decimal" minFractionDigits={0} maxFractionDigits={2} step={0.01} showButtons={true} min={minKef} useGrouping={false} placeholder={`Mindestwert ${minKef}`} />
                </div>
                <div className="game_bet_block_row">
                    {buttonBet}
                </div>
                <Toast ref={ toast } />
            </div>
        );

    } else {

        return(
            <div className="block_game__bet">
              <div className="game_bet__row">
                <a className="login_a" href="/login"><button>Login to play</button></a>
              </div>
              <div className="game_bet__row">
                or <a href="/register">register</a>
              </div>
            </div>
        );

    }
}

export default BlockBet;