import React, { useRef } from "react"

const Chat = data => {

  function jwtDecode(t) {
    let token = {};
    token.raw = t;
    token.header = JSON.parse(window.atob(t.split('.')[0]));
    token.payload = JSON.parse(window.atob(t.split('.')[1]));
    return (token)
  }
  function getDataUser() {

    let dataToken = localStorage.getItem('Token');

    let token = {};

    if (dataToken) {

      dataToken = JSON.parse(dataToken);

      token = jwtDecode(dataToken);

      if ('userName' in token.payload) {

        return { username: token.payload.userName, isLoginUser: true };

      }

    }

    return { username: 'No name', isLoginUser: false };
  }

  const { username, isLoginUser } = getDataUser();
  const messages = data.messages;
  const showHeader = data.showHeader;

  const textMessage = useRef(null)

  const sendMessage = () => {
    const text = textMessage.current.value;

    const date = new Date();

    const message = {
      sender: username,
      text,
      date
    }

    data.onSendMessage(message);

    textMessage.current.value = ''
  }

  return (

    <div className="block_chat">
      {(showHeader !== false ? <div className="block_chat__header">Chat</div> : '')}
      <div className="block_chat__body scrollStyle">
        {messages.map((msg, index) => (
          <div className="chat_row" key={'msg' + index}>
            <div className="chat_row__time">{ new Date(msg.date).toLocaleTimeString('ru-RU', { hour: 'numeric', minute: 'numeric' }) }</div>
            <div className="chat_row__name">{ msg.sender }</div>
            <div className="chat_row__text">{ msg.text }</div>
          </div>
        ))}
        
      </div>
      <div className="block_chat__footer">
        <input ref={ textMessage } type="text" placeholder="Write text ..." disabled={ !isLoginUser } />
        <button onClick={sendMessage} disabled={ !isLoginUser }>Send</button>
      </div>
    </div>
  )
}

export default Chat