import { useRef } from "react";

const DepositInfo = () => {

    const winModal = useRef(null);

    function closeWindow() {
        winModal.current.classList.add('--hidden');
    }

    let balance = localStorage.getItem('Balance');
    if (balance) balance = JSON.parse(balance);
    

    if (!balance) {
        return ('');
    } else {
        if (balance.btc > 0 || balance.eth > 0) {
            return ('');
        }
    }

    return(
        <div className="deposit-notification" ref={ winModal }>
            A positive balance is required to start the game.<br />
            <a href="/profile">Click here to deposit and play</a>
            <span className="close-win" onClick={ closeWindow }>✖</span>
        </div>
    )
}

export default DepositInfo;