import { Dialog } from 'primereact/dialog';

import { Profile, Settings, FAQ, Terms } from './Profile';

const ModalProfile = (data) => {

    function setContent() {
        return {
            'PROFILE': <Profile />,
            'SETTINGS': <Settings />,
            'HELP & FAQS': <FAQ />,
            'TERMS': <Terms />
        }[data.header];
    }

    return (
        <Dialog maximizable={true} header={ data.header } dismissableMask={ data.dismissableMask } position={ data.position } style={ data.style } visible={ data.visible } resizable={ data.resizable } onHide={ data.onHide }>
            { setContent() }
        </Dialog>
    )
}

export default ModalProfile