import React from 'react'
import { Routes, Route, Navigate } from 'react-router-dom';

import Home from './pages/Home';
import Register from './pages/Register';
import Login from './pages/Login';
import Profile from './pages/Profile';
import Bots from './pages/Bots';
import Messages from './pages/Messages';
import Users from './pages/Users';
import Settings from './pages/Settings';
import AccountSettings from './pages/AccountSettings';

export const useRoutes = (authData) => {
  if (authData) {
    const payload = authData.payload;
    const userType = payload.userType;
    if (userType === 'User' || userType === 'Bot') {
      return (
        <Routes>
          <Route path="/" exact element={<Home />}></Route>
          <Route path="/profile" element={<Profile />}></Route>
          <Route path="*" element={<Navigate to="/" />} />
        </Routes>
      )
    } else if (userType === 'Admin') {
      return (
        <Routes>
          <Route path="/" exact element={<Bots />}></Route>
          <Route path="/admin/bots" element={<Bots />}></Route>
          <Route path="/admin/messages" element={<Messages />}></Route>
          <Route path="/admin/users" element={<Users />}></Route>
          <Route path="/admin/settings" element={<Settings />}></Route>
          <Route path="/admin/account-settings" element={<AccountSettings />}></Route>
          <Route path="*" element={<Navigate to="/" />} />
        </Routes>
      )
    } else {
      return (
        <Routes>
          <Route path="/" exact element={<Home />}></Route>
          <Route path="/login" element={<Login />}></Route>
          <Route path="/register" element={<Register />}></Route>
          <Route path="*" element={<Navigate to="/" />} />
        </Routes>
      )
    }
  } else {
    return (
      <Routes>
        <Route path="/" exact element={<Home />}></Route>
        <Route path="/login" element={<Login />}></Route>
        <Route path="/register" element={<Register />}></Route>
        <Route path="*" element={<Navigate to="/" />} />
      </Routes>
    )
  }
}