import axios from 'axios';
import { useEffect, useState } from 'react';
import SideBar from '../components/Admin/Sidebar';
import Table from '../components/General/Table';
import ModalWindow from '../components/Admin/ModalWindow';

import { Button } from 'primereact/button';

const Users = () => {

    const [tableData, $tableData] = useState([]);
    const [displayBasic, setDisplayBasic] = useState(false);
    const [userName, setUserName] = useState('');

    const dataTable = {
        head: ['Login', 'Registration date', 'Game played', 'Input money', 'Output money', 'BTC', 'ETH', 'Action'],
        body: tableData
    }

    function toggleActiveLi(e) {
        document.querySelector('.active').classList.remove('active');
        e.target.classList.add('active')
    }
    function openWindow(e) {
        let tr = e.target.closest('tr');
        let td = tr.querySelector('td');
        setUserName(td.textContent);
        setDisplayBasic(true);
    }

    async function getUsers() {
        const req = await axios.post('/api/find/users', { typeUser: 'User' });

        if (req.data.ok) {
            let users = req.data.users.map(item => {
                return [ 
                    item.login, 
                    new Date(item.registerDate).toLocaleDateString('ru-RU'),
                    '-', '-', '-',
                    (item.balance.btc).toFixed(5),
                    (item.balance.eth).toFixed(5),
                    <Button onClick={ openWindow } icon="pi pi-pencil" className="p-button-rounded p-button-info" />
                ]
            })

            $tableData(users);
        }
    }

    function hideModal() {
        setDisplayBasic(false);
        getUsers();
    }

    useEffect(() => {
        getUsers();
    }, [])

    return (
        <div className='block_admin'>
            <SideBar />
            <div className="admin_content">
                <div className="admin_content_header">
                    <div className="admin_content_header_item">
                        <ul>
                            <li onClick={toggleActiveLi} className='active'>All users</li>
                            <li onClick={toggleActiveLi}>Online users</li>
                        </ul>
                    </div>
                </div>
                <div className="admin_content_block">
                    <div className="admin_content_block_body scrollStyle">
                        <Table data={ dataTable } />
                    </div>
                </div>
            </div>
            <ModalWindow header="Refill - " userName={userName}  dismissableMask={ true } position='top' style={{ width: '40vw' }} visible={displayBasic} resizable={ true } onHide={hideModal} />
        </div>
    )
}

export default Users