import React, { useState, useEffect } from "react";
import Chat from '../components/Site/Chat';
import Header from '../components/Site/Header';
import Table from '../components/General/Table';
import Game from '../components/Site/Game';
import BlockBet from '../components/Site/BlockBet';
import DepositInfo from '../components/Site/DepositInfo';

import io from 'socket.io-client'

const URL_SERVER = ''; //http://localhost:80

let TOKEN = localStorage.getItem('Token');
if (TOKEN) TOKEN = JSON.parse(TOKEN);

const socket = io(URL_SERVER, { reconnection: true, reconnectionAttempts: 10, auth: { token: TOKEN } });

const windowInnerWidth = window.innerWidth;

const Home = () => {

  let [messages, $messages] = useState([]);
  let [players, $players] = useState([]);
  let [value, $value] = useState('Loading ...');
  let [countUsers, $countUsers] = useState('...');
  let [amountBTC, $amountBTC] = useState('...');
  let [amountETH, $amountETH] = useState('...');
  let [graphData, $graphData] = useState({});
  let [results, $results] = useState([]);

  const [isUserPlays, setIsUserPlays] = useState(false);

  const playersTable = {
    head: ['User', '@', 'Bet', 'Profit'],
    body: players
  }

  useEffect(() => {

    //#region 
    function sortPlayers() {
      let arr = _players;

      let nums = arr.filter(row => row[1] !== '...');
      let texts = arr.filter(row => row[1] === '...');

      texts.sort((a, b) => {
        let aa = Number(a[2].replace(/[^0-9,\.]/gi, ''));
        let bb = Number(b[2].replace(/[^0-9,\.]/gi, ''));

        if (aa > bb) return -1;
        if (aa > bb) return 1;
        return 0;

      });

      nums.sort((a, b) => {
        let aa = Number(a[1]);
        let bb = Number(b[1]);
        if (aa > bb) return -1;
        if (aa > bb) return 1;
        return 0;
      });

      return [...texts, ...nums];

    }
    //#endregion

    let _users = [];
    let _players = [];
    let _gameStatus = null;
    let _count = 0;
    let _kef = 1;
    let _players_win = [];

    let _btc = 0;
    let _eth = 0;

    socket.on('connect', () => {
      console.log('connecting ...');
    });
  
    socket.on('getMessages', (data) => {
      $messages(data);
      document.querySelector('.block_chat__body').scrollTop = document.querySelector('.block_chat__body').scrollHeight;
    })

    socket.on('connectionData', (data) => {
      const { arrMsgs, curRes, curStatus, curVal, curCount, curPlayers, balance } = data;

      if (balance) {
        localStorage.setItem('Balance', JSON.stringify(balance));
      }

      _players_win = [];
  
      $messages(arrMsgs);
      $results(curRes);
      _gameStatus = curStatus;
      _count = curCount;
      _players = curPlayers;

      $players(sortPlayers(_players));
      $countUsers(_players.length);

      // Данные для восстановления графика
      let val = curVal;

      if (_gameStatus === 'Game') {
        _kef = Number(val / 100).toFixed(2)
        val = `${_kef}x`;
      } else if (_gameStatus === 'Results') {
        val = `Crashed @ ${_kef}`;
      } else if (_gameStatus === 'Pause') {
        val = `Next game in ${Number(val / 1000).toFixed(1)} s`;
      }
      let gameInfo = { recovery: true, gameStatus: _gameStatus, value: curVal / 100, count: _count };
      $graphData({...gameInfo});

      _btc = 0;
      _eth = 0;

      for (let player of _players) {
        let amount = Number(player[2].replace(/[^0-9,\.]/gi, ''));
        let currency = player[2].split(' ')[1];

        if (amount) {
          if (currency === 'BTC') _btc += Number(amount);
          if (currency === 'ETH') _eth += Number(amount);
        }
      }

      (_btc === 0) ? _btc = '...' : _btc = Number(_btc).toFixed(2);
      (_eth === 0) ? _eth = '...' : _eth = Number(_eth).toFixed(2);

      $amountBTC(_btc);
      $amountETH(_eth);

      if (curStatus === 'Results') {
        _kef = Number(Math.trunc(curVal) / 100).toFixed(2)
      }

      document.querySelector('.block_chat__body').scrollTop = document.querySelector('.block_chat__body').scrollHeight;

    });

    // LOGOUT
    socket.on('logout', () => {
      localStorage.removeItem('Token');
      let balance = false;
      localStorage.setItem('Balance', JSON.stringify(balance));
    })

    // change status game
    socket.on('cs', data => {

      _gameStatus = data;

      let _isUserPlays = isUserPlays;
      if (data === 'Results') _isUserPlays = false;

      setIsUserPlays(_isUserPlays);

    });

    // game_start
    socket.on('gst', data => {
      _count = 0;
      
      _btc = 0;
      _eth = 0;

      let name = getUserName();
      let _isUserPlays = isUserPlays;

      _players = _users.map(user => {
        let login = user.u;
        let amount = data.p.find(row => row.u === login).a;
        let currency = user.c;

        if (name === login) _isUserPlays = true;

        if (currency === 'BTC') _btc += Number(amount);
        if (currency === 'ETH') _eth += Number(amount);

        return [login, '...', `${amount} ${user.c}`, '...'];
      });

      _players_win = [];

      setIsUserPlays(_isUserPlays);

      $players(sortPlayers(_players));

      $countUsers(_users.length);
      $amountBTC(Number(_btc).toFixed(2));
      $amountETH(Number(_eth).toFixed(2));

    });

    // game_step
    socket.on('gs', data => {
      
      let val = data;

      if (_gameStatus === 'Game') {
        _kef = Number(val / 100).toFixed(2)
        val = `${_kef}x`;
      } else if (_gameStatus === 'Results') {
        val = `Crashed @ ${_kef}`;
      } else if (_gameStatus === 'Pause') {
        val = `Next game in ${Number(val / 1000).toFixed(1)} s`;
      }
      
      _count += 1
      let gameInfo = { gameStatus: _gameStatus, value: data / 100, count: _count };
      $value(val);
      $graphData({...gameInfo});

    });

    // player_win
    socket.on('pw', data => {

      let _isUserPlays = isUserPlays;

      let name = getUserName();

      _players = _players.map(user => {

        if (user[0] === data.u) {
          if (user[0] === name) _isUserPlays = false;
          let kef = Number(data.k / 100).toFixed(2);
          let amount = Number(user[2].replace(/[^0-9,\.]/gi, ''));
          let currency = user[2].split(' ')[1];
          let profit = Number(amount * kef - amount).toFixed(2);
          return [ data.u, kef, user[2], `${profit} ${currency}` ];
        } else {
          if (user[0] === name) _isUserPlays = true;
          return user;
        }
      });

      _players_win.push(data.u);

      for (let u of _players_win) {
        if (u === name) _isUserPlays = false;
      }

      setIsUserPlays(_isUserPlays);

      $players(sortPlayers(_players));

    });

    // game_crashed
    socket.on('gc', data => {

      //_gameStatus = 'Results';

      _players = _players.map(user => {
        if (user[1] === '...') {
          let amount = Number(user[2].replace(/[^0-9,\.]/gi, ''));
          let currency = user[2].split(' ')[1];
          let profit = -amount;

          return [user[0], '...', user[2], `${profit} ${currency}`];

        } else {
          return user;
        }
      });

      $players(sortPlayers(_players));

      _users = [];

      $results(data.r);
      _players = [];

    })

    // player_bet
    socket.on('pb', user => {

      //_gameStatus = 'Pause';

      if (user) {

        let isUser = _users.find(row => row.u === user.u);
        if (!isUser) {
          _users.push(user);
        }

        let isPlayer = _players.find(row => row[0] === user.u);
        if (!isPlayer) {
          _players.push([user.u, '...', '?', '...']);
        }
      }

      $players(_players);

      $countUsers('?');
      $amountBTC('...');
      $amountETH('...');

    });

  }, []);

  const sendMessage = (message) => {
  
    // Если нет текста, не отправлять сообщение
    if (message.text) {
      socket.emit('sendMessage', message)
    }

  }

  function changeActiveItemMenu(e) {

    let activeItem = document.querySelector('.block_menu .active');
    if (activeItem) {
      let id = activeItem.id;
      activeItem.classList.remove('active');
      document.querySelector(`.${id}`).classList.add('--hidden');
    }

    let id = e.target.id;
    e.target.classList.add('active');
    document.querySelector(`.${id}`).classList.remove('--hidden');

  }

  function getUserName() {
    function jwtDecode(t) {
      let token = {};
      token.raw = t;
      token.header = JSON.parse(window.atob(t.split('.')[0]));
      token.payload = JSON.parse(window.atob(t.split('.')[1]));
      return (token)
    }
    let token = localStorage.getItem('Token');
    if (token) {
      token = jwtDecode(JSON.parse(token));
      
      return token.payload.userName;
    }

    return false;
  }

  if (windowInnerWidth < 700) {

    return (
      <div className="main">
        <Header socket={ socket } />
        <div className="content">
          <DepositInfo />
          <div className="content_left">
            <div className="block_game">
              <div className="block_game__game">
                <div className="game__graph">
                  <Game data={ graphData } value={ value } />
                </div>
                <div className="game__results">
                  {
                    results.map((el) => (
                      <div className={'item_result ' + (el.k >= 2 ? "color-green" : 'color-red') } key={ Math.random().toString(36).substring(3) }>{ Number(el.k).toFixed(2) }</div>
                    ))
                  }
  
                </div>
              </div>
              <BlockBet socket={ socket } isUserPlays={ isUserPlays } gameStatus={graphData.gameStatus} />
            </div>
            <div className="block_menu">
              <div className="block_menu__item active" id="block_chat" onClick={ changeActiveItemMenu }>Chat</div>
              <div className="block_menu__item" id="block_players" onClick={ changeActiveItemMenu }>Players</div>
            </div>
            <Chat onSendMessage={ sendMessage } messages={ messages } showHeader={ false } />
            <div className="block_players --hidden">
              <div className="players_body scrollStyle">
                <Table data={ playersTable } clasColor={ true } />
              </div>
              <div className="players_footer">
                <div className="players_footer__item">
                  <p>Players: <span>{ countUsers }</span></p>
                </div>
                <div className="players_footer__item">
                  <p>Betting: <span>{ amountETH }</span> ETH and <span>{ amountBTC }</span> BTC</p>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    )

  }

  return (
    <div className="main">
      <Header socket={ socket } />
      <div className="content">
        <DepositInfo />
        <div className="content_left">
          <div className="block_game">
            <div className="block_game__game">
              <div className="game__graph">
                <Game data={ graphData } value={ value } />
              </div>
              <div className="game__results">
                {
                  results.map((el) => (
                    <div className={'item_result ' + (el.k >= 2 ? "color-green" : 'color-red') } key={ Math.random().toString(36).substring(3) }>{ Number(el.k).toFixed(2) }</div>
                  ))
                }

              </div>
            </div>
            <BlockBet socket={ socket } isUserPlays={ isUserPlays } gameStatus={graphData.gameStatus} />
          </div>
          <Chat onSendMessage={ sendMessage } messages={ messages } />
        </div>
        <div className="content_right">
          <div className="block_players">
            <div className="players_body scrollStyle">
              <Table data={ playersTable } clasColor={ true } />
            </div>
            <div className="players_footer">
              <div className="players_footer__item">
                <p>Players: <span>{ countUsers }</span></p>
              </div>
              <div className="players_footer__item">
                <p>Betting: <span>{ amountETH }</span> ETH and <span>{ amountBTC }</span> BTC</p>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  )
}

export default Home