import { useRef, useState } from "react"
import { Toast } from 'primereact/toast';
import Header from "../components/Site/Header";

import { Password } from 'primereact/password';
import { InputText } from 'primereact/inputtext';


const Login = () => {

  const [password, setPassword] = useState('');

  const toast = useRef(null);

  const token = localStorage.getItem('Token')

  if (token) document.location.href = '/admin/bots'

  const loginHengler = async () => {
    const email = document.getElementById('emailInput');

    const form = {
      login: email.value,
      password: password
    }

    const request = await fetch('/api/auth/login', {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json'
      },
      body: JSON.stringify(form)
    })

    const response = await request.json()

    if (response.ok) {
        if (response.token && response.userId) {
            const token = response.token;
      
            localStorage.setItem('Token', JSON.stringify(token));
      
            document.location = '/admin/bots'
        } else {
          toast.current.show({severity:'error', summary: 'Error Message', detail: response.message, life: 5000});
        }
    } else {
      toast.current.show({severity:'error', summary: 'Error Message', detail: response.message, life: 5000});
    }
  }

  return (
    <div className="main">
      <Header isLoginUser={ false } />
      <div className="login">
        <div className="login_block">
          <h2 className="login_title">LOG IN</h2>

          <div className="login_value">

            <div className="login_item">
              <p>Username</p>
              <InputText id="emailInput" placeholder="press your Username" className="w-12" />
            </div>

            <div className="login_item">
              <p>Password</p>
              <Password value={ password } onChange={(e) => setPassword(e.target.value)} feedback={false} toggleMask style={{ width: '100%' }} id="passwordInput" className="w-12" placeholder="press your Password" />
            </div>

          </div>

          <div className="forgot">
            <a href="/forgot-password">Forgot your password?</a>
          </div>

          <div className="btn-container--center">
            <button onClick={loginHengler} className="btn btn--center">Login</button>
          </div>

        </div>
        <Toast ref={ toast } />
      </div>
    </div>
  )
}

export default Login