import { useRef, useState, useEffect } from 'react';

import SideBar from '../components/Admin/Sidebar';
import { Toast } from 'primereact/toast';

import { Password } from 'primereact/password';
import { Button } from 'primereact/button';

import axios from 'axios';

const AccountSettings = () => {

    const [secretKey, setSecretKey] = useState(null);
    const [newPassword, setNewPassword] = useState(null);

    const toast = useRef(null);

    async function changePassword() {
        const req = await axios.post('/api/auth/change-password-admin', {
            secretKey: secretKey,
            newPassword: newPassword
        });

        if (req.data.ok) {
            toast.current.show({ severity: 'success', summary: 'Info Message', detail: 'Password changed ', life: 5000});
        } else {
            toast.current.show({ severity: 'error', summary: 'Error Message', detail: req.data.text, life: 5000});
        }
    }

    return (
        <div className='block_admin'>
            <SideBar />
            <div className="admin_content">
                <div className="admin_content_block admin_content_block--max">
                    <h3>Account Settings</h3>
                    <div className="field grid mt-2">
                        <div className="col">
                            <Password value={secretKey} onChange={(e) => setSecretKey(e.target.value)} placeholder='Secret key' feedback={false} toggleMask={true} />
                        </div>
                    </div>
                    <div className="field grid">
                        <div className="col">
                            <Password value={newPassword} onChange={(e) => setNewPassword(e.target.value)} placeholder='New Password' feedback={false} toggleMask={true} />
                        </div>
                    </div>
                    <div className="field grid">
                        <div className="col">
                            <Button onClick={changePassword} className='p-button-success'>Change Password</Button>
                        </div>
                    </div>
                </div>
            </div>
            <Toast ref={ toast } />
        </div>
    );
    
}

export default AccountSettings